import React from 'react';
import {Redirect} from 'react-router-dom';

class FbLogin extends React.Component {
  state = {
    redirect: false
  }

   checkLoginState = () => {
     return new Promise((res, rej) => {
        window.FB.getLoginStatus(function(response) {
            res(response)
        });
      })
    }
  loadFbApi() {
    return new Promise((res, rej) => {
      if (!window.FB) { 
          (function(d, s, id) {
            var js, fjs = d.getElementsByTagName(s)[0];
            if (d.getElementById(id)) return;
            js = d.createElement(s); js.id = id;
            js.src = "//connect.facebook.net/en_US/sdk.js";
            fjs.parentNode.insertBefore(js, fjs);
          }(document, 'script', 'facebook-jssdk'));
          window.fbAsyncInit = function() {
              let FB_APP_ID = 1796391010517069;
              window.FB.init({
                  appId      : FB_APP_ID,
                  cookie     : true, 
                  // the session
                  xfbml      : true, 
                  version    : 'v9.0' 
              })
              window.FB.AppEvents.logPageView();
              console.log('fired')
              res()
        } 
      } else {
          res()
      }
      })
    }

    componentDidMount() {
        this.loadFbApi()
        .then(() => {
          let fire = this.checkLoginState.bind(this)
          fire()
          .then(async (response) => {
            return new Promise(async (resolve, rej) => {
              if (response.status === 'unknown'||response.status === 'not_authorized') {
                  console.log('here')  
                  new Promise((resolve2, rej) => {
                    window.FB.login(function(res) {
                      console.log('logged in', res.authResponse.accessToken)
                      let access = res.authResponse.accessToken;
                      resolve2(access)
                    })
                  }).then(access => {
                    this.props.redirectPageLogout(false)
                    this.props.setAccess(access)
                    resolve(access)
                  })
              } else {
                  console.log('already logged in', response)
                  this.props.redirectPageLogout(false)
                  await this.props.setAccess(response.authResponse.accessToken)
                  resolve(response.authResponse.accessToken)
              }            
          })
          .then(access => {
            console.log('a',access)
            if (access) {
              new Promise((res, rej) => {
                window.FB.getLoginStatus(resp => {
                  console.log(resp)
                    window.FB.api('/me/accounts?fields=id', {access_token: resp.authResponse.accessToken}, (response) => {
                        console.log('RESPONSE',response)
                        if (response) {
                          res()
                        } else {
                          rej()
                        }
                    })
                })
              })
              .then(() => {
                if(this.props.accessToken.length > 1) {
                  console.log('here')
                  this.setState({redirect: true})
                  console.log('redirect', this.state.redirect)
                  setTimeout(() => {this.forceUpdate()}, 1000)
                }
              })
              .catch(err => console.log(err))
            }
          })
          })
      })
    }



    render() {
      const {loggedIn} = this.props;
      const {redirect} = this.state;
      console.log('new', loggedIn, redirect)
        return (
            <React.Fragment>
              {redirect === true ? <Redirect to="/dashboard" /> : null}
                <div id="fb-root">
                  {/* <div onClick={this.handleClick}>FACEBOOK */}
                  <div 
                  data-onlogin={() => console.log('data login')}
                  className="fb-login-button" 
                  data-width="300" 
                  data-size="large" 
                  data-button-type="continue_with" 
                  data-layout="default" 
                  data-auto-logout-link="false" 
                  data-use-continue-as="false"
                  scope='read_insights,instagram_basic,pages_show_list,instagram_manage_comments,instagram_manage_insights,pages_read_engagement' 
                  >
                  </div>
                </div>
            </React.Fragment>
        )
    }
}

export default FbLogin;